.signUpPage{

    display: flex;
    margin:5% auto;
    justify-content: space-around;
    align-items: center;
}

.imgSection{
    width: 20%;
}

.formSection{
    width: 60%;
}

.formInput{
    width: 50%;
    font-size: medium;
    border-radius: 25px;
    padding:1%;
}

.signUpBtn{
    margin: 2% 0%;
    border-radius: 25px;
    background-color: black;
    border-color: #cccc;
    color: gold;
    font-size: medium;
    padding: 2%;
    cursor: pointer;
}