.welcomePage{
    width: 80%;
    margin: 5% auto;
}

.welcomePageHead{
    text-align: center;
    color: red;
    font-family: 'Times New Roman', Times, serif;
    font-size: xx-large;
    font-weight: bold;
}

.welcomePageTagline{
    text-align: center;
    font-size: x-large;
    font-family: 'Times New Roman', Times, serif;
    color: blue;
}

.midParaContainer{
    margin: 2% 0%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.midPara{
    width:40%;
    padding: 2%;
    border: 5px solid brown;
    border-radius: 15px;
}

.introParas{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: larger;
    line-height: 1.6;
}