.loginPage{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin:5% auto;
}

.loginIcon{
    width: 20%;
}

.loginDetails{
    width: 60%;
}

.loginInput{
    width: 50%;
    font-size: medium;
    border-radius: 25px;
    padding: 1%;
}

.loginBtn{
    margin: 2% 0%;
    border-radius: 25px;
    background-color: black;
    border-color: #cccc;
    color: gold;
    font-size: medium;
    padding: 2%;
    cursor: pointer;
}