.profilePage{
    margin: 5% 0%;
}

.headProfile{
    color: red;
    font-size: x-large;
    text-align: center;
    margin: 2% 0%;
}

.postBox{
    margin: 2% auto;
    width: 50%;
}

.caption{
    font-size: medium;
    color: black;
    font-family: 'Courier New', Courier, monospace;
}

.likeBtn{
    margin: 2% 0%;
    border-radius: 25px;
    background-color: black;
    border-color: #ccc;
    color: gold;
    width: 10%;
    cursor: pointer;
    display: flex;
    justify-content: space-around;
}


.likeInp{
    visibility: hidden;
}

.likeBtnInp{
    background-color: black;
    color: gold;
    width: 40%;
}

.postItem{
    margin:5% 0%
}