.navbar{
    background-color: #F0BB78;
    display:flex;
    justify-content: space-around;
    align-items: center;
    padding: 0.5%;
    position: fixed;
    top: 0;
    width: 100%;
    
}

.navHead{
    width: 20%;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: bold;
    font-size: xx-large;
    color: #133E87;
}

.navLinks{
    width: 40%;
    display:flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: large;
    font-weight: bold;
}

.navItems{
    text-decoration: none;
}

.profileName{
    width: 5%;
    height: 1%;
    font-size: medium;
    font-style: italic;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
